import React from 'react'
import {Card,Col,Row,Button,Tooltip,notification,Select} from 'antd'
import CustomBreadcrumb from '../../../components/CustomBreadcrumb/index'
import TypingCard from '../../../components/TypingCard'

class NotificationDemo extends React.Component{
  state = {
    placement:''
  }
  openNotification(obj){
    notification.open({
      message: 'Notification Title',
      description: 'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
      ...obj
    })
  }
  openNotificationType(type){
    notification[type]({
      message: 'Notification Title',
      description: 'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
    });
  }
  customNotification(){
    const key = Date.now()
    notification.open({
      message: 'Notification Title',
      description: 'A function will be be called after the notification is closed (automatically after the "duration" time of manually).',
      key,
      btn:<Button type="primary" onClick={() => notification.close(key)}>Confirm</Button>
    })
  }
  render(){
    const placement = this.state.placement
    const cardContent = ` 
          <ul class="card-ul">
            <li></li>
            <li></li>
            <li></li>
          </ul>`
    return (
      <div>
        <CustomBreadcrumb arr={['','']}/>
        <TypingCard source={cardContent} height={234}/>
        <Row gutter={16}>
          <Col span={12}>
            <Card bordered={false} className='card-item'>
              <Tooltip title='4.5 ' placement='right'>
                <Button type='primary' onClick={this.openNotification}></Button>
              </Tooltip>
            </Card>
          </Col>
          <Col span={12}>
            <Card bordered={false} className='card-item'>
              <Tooltip title='duration: 0' placement='right'>
                <Button type='primary' onClick={()=>this.openNotification({duration:0})}></Button>
              </Tooltip>
            </Card>
          </Col>
          <Col span={12}>
            <Card bordered={false} className='card-item'>
             <Button type='primary' onClick={()=>this.openNotificationType('success')}></Button>&emsp;
             <Button type='primary' onClick={()=>this.openNotificationType('info')}></Button>&emsp;
             <Button type='primary' onClick={()=>this.openNotificationType('warning')}></Button>&emsp;
             <Button type='primary' onClick={()=>this.openNotificationType('error')}></Button>
            </Card>
          </Col>
          <Col span={12}>
            <Card bordered={false} className='card-item'>
              <Tooltip title=' style  className ' placement='right'>
                <Button type='primary' onClick={()=>this.openNotification({style:{width: 600, marginLeft: 335 - 600,}})}></Button>
              </Tooltip>
            </Card>
          </Col>
          <Col span={12}>
            <Card bordered={false} className='card-item'>
              <Tooltip title='' placement='right'>
                <Button type='primary' onClick={this.customNotification}></Button>
              </Tooltip>
            </Card>
          </Col>
          <Col span={12}>
            <Card bordered={false} className='card-item'>
              <Select defaultValue='topRight' style={{width:160}} onChange={v=>this.setState({placement:v})}>
                <Select.Option value='topLeft'>topLeft</Select.Option>
                <Select.Option value='topRight'>topRight</Select.Option>
                <Select.Option value='bottomLeft'>topLeft</Select.Option>
                <Select.Option value='bottomRight'>bottomRight</Select.Option>
              </Select>&emsp;&emsp;
              <Button onClick={()=>this.openNotification({placement})}></Button>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default NotificationDemo


